<template>
  <div>
    <div class="tip-box" v-show="confirm">
      <div class="tip">
        <p>确定修改信息</p>
        <div class="tip-btn" @click="cancel_confirm">取消</div>
        <div class="tip-btn" @click="save_confirm" style="margin-left:10px;background:#8FC4FE;color:#fff;">确认</div>
      </div>
    </div>
    <div class="main">
      <div
        class="body clearfix"
        @mousedown="body_mousedown"
        @mousemove="body_mousemove"
        @mouseup="body_mouseup"
        id="body"
      >
        <div class="my-table">
          <div class="my-thead">
            <div class="my-thead-left">
              时间
            </div>
            <ul class="clearfix">
              <li v-for="(item,index) in dataList" :key="index" >{{item.name}}</li>
            </ul>
          </div>
          <div class="my-tbody"  @scroll="myCellBoxScroll">
            <div class="my-tr clearfix"  v-for="(item,index) in tableTime" :key="index">
              <div class="my-hour">{{item}}</div>
              <div class="my-minute-box">
                <div class="my-minute">00</div>
                <div class="my-minute">15</div>
                <div class="my-minute">30</div>
                <div class="my-minute">45</div>
              </div>
              <div class="my-cell-box clearfix">
                <div class="my-cell-lie" v-for="(item2,index2) in dataList" :key="index+'-'+index2">
                  <ul >
                    <li class="mytd" ></li>
                    <li ></li>
                    <li ></li>
                    <li ></li>
                  </ul>
                </div>

              </div>
            </div>
            <div class="my-tbody-copy" id="mytbodyCopy" @mousedown="add_mousedown">
              <div class="app-new" v-if="appNewShow" :style="`width:${appNew.width}px;height:${appNew.height}px;top:${appNew.top}px;left:${appNew.left}px;`">
                <p>新建预约</p>
                <div class="app-new-time" :style="`bottom:${appNew.timeBottom}px`">{{appNew.startTime}} - {{appNew.endTime}}</div>
              </div>
              <template v-for="(item,index) of appDataCopy.appList">
                <div
                  v-for="(item2,index2) of item"
                  :key="index+'-'+index2"
                  class="chooser"
                  :style="`width:${item2.width}px;height:${item2.height}px;top:${item2.top}px;left:${item2.left}px;z-index:${item2.zindex};`"
                  ref="cho"
                  @mousedown="box_mousedown"
                  @mouseup="box_mouseup(index,index2,$event)"
                >
                  <div class="title"  @mousedown="title_mousedown(index,index2,$event)">{{item2.info.patientName}}</div>
                  <div class="content" >
                    {{item2.info.appointProject}}
                    <div class="content-time">{{item2.info.startTime}} - {{item2.info.endTime}}</div>
                  </div>
                  <div class="footline" @mousedown="footline_mousedown(index,index2,$event)"></div>
                </div>
              </template>
              <div class="my-time-line" :style="`top:${timeLineTop}px`"></div>
            </div>

          </div>
        </div>

      </div>
    </div>

  </div>

</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      stateList:[],
      waitList:[],
      appDetail:{
        show:false,
        tab:1,
        left:0,
        top:0,
        info:{
          appointNow:[],
          appointAfter:[],
          appointBefore:[],
        },
      },
      confirm:false,
      isChange:false,
      appNewShow:false,
      appNewIndex:-1,//新建的预约属于哪一列
      appNewHeightNum:1,
      appNew:{
        height: 0,
        width:0,
        top: 0,
        left:0,
        startTime:'',
        endTime:'',
        timeBottom:0,
      },
      mainInitHeight:0,//main div 的高度
      //记录点击得到类型，即鼠标是在哪里被点下的：title--标题；footline--底部拉伸处
      type: "",
      //标记鼠标已经被按住
      isHold: false,
      //格块的宽度、高度，用变量记录，用在函数的公式中，以防止后面发生改变
      //公式中不出现与格块宽高有关的数字
      cellWidth: '',
      cellHeight: '',
      //记录原点坐标

      holdIndex: 0,
      holdIndex2: 0,
      holdIndexNew:0,
      offsetY:0,
      offsetX:0,
      originX: 0,
      originY: 0,
      originTop: 0,
      nowDate:'',//今天的时间戳
      mydate:'',
      listPage:{
        pagesize:7,
        row:1,
        lastPage:1,
        total:0,
      },
      workTime1:{//上午上班时间
        start:'08:00',
        end:'12:00'
      },
      workTime2:{//下午上班时间
        start:'12:00',
        end:'24:00'
      },
      workTimeNum:{
        am1:1,
        am2:0,
        pm1:0,
        pm2:0,
      },
      holdInfo:{},//操作的那个预约的数据
      liAppNum:0,//这一列里一行最多几个预约
      tdNum:'',//td 总数量
      tableTime:[],
      dataList:[
        {name:'星期一',list:[],},
        {name:'星期二',list:[],},
        {name:'星期三',list:[
            {

              startTime:'08:45',
              endTime:'9:45',
              patientName:'张三'
            },
          ],},
        {name:'星期四',list:[],},
        {name:'星期五',list:[
            {

              startTime:'08:15',
              endTime:'11:00',
              patientName:'张三'
            },
            {

              startTime:'08:45',
              endTime:'10:00',
              patientName:'张三'
            },
          ],},
        {name:'星期六',list:[],},
        {name:'星期日',list:[],},

      ],
      dataListCopy:[],
      appData: {
        appNum:[],
        appIndex:[],//每个td里的预约的index
        appTd:[],
        appList:[],
      },
      appDataCopy:{
        appNum:[],
        appIndex:[],//每个td里的预约的index
        appTd:[],
        appList:[],
      },
      timeSetInterval:'',//定时器
      timeLineTop:0,
      cancel:{
        show:false,
        id:"",
        name:'',
      },
      detailConfirm:{
        show:false,
        id:"",
        name:'',
      },
    };
  },
  created(){
    let time1=this.workTime1.start;//开始时间和结束时间计算格子个数 并添加到appData.appNum
    let time2=this.workTime2.end;
    let startHour=time1.split(':')[0]
    let startMinute=time1.split(':')[1]
    let endHour=time2.split(':')[0]
    let endMinute=time2.split(':')[1]

    //处理时间td的列表
    this.tableTime=[]
    for(let i=Number(startHour);i<Number(endHour);i++){
      if(i<10){
        this.tableTime.push('0'+i)
      }else{
        this.tableTime.push(''+i)
      }
    }
    //td 总数量
    this.tdNum=((Number(endHour)-Number(startHour))*60+(Number(endMinute)-Number(startMinute)))/15

    let time12=this.workTime1.end;
    let time21=this.workTime2.start;
    let startHour12=time12.split(':')[0]
    let startMinute12=time12.split(':')[1]
    let endHour21=time21.split(':')[0]
    let endMinute21=time21.split(':')[1]
    this.workTimeNum.am2=((Number(startHour12)-Number(startHour))*60+(Number(startMinute12)-Number(startMinute)))/15
    this.workTimeNum.pm1=((Number(endHour21)-Number(startHour))*60+(Number(endMinute21)-Number(startMinute)))/15
    this.workTimeNum.pm2=this.tdNum;



  },
  mounted() {
    this.$nextTick(function () {
      //my-thead 的宽度等于my-tbody 去掉滚动条的宽度
      document.getElementsByClassName('my-thead')[0].style.width=document.getElementsByClassName('my-tbody')[0].clientWidth+'px'
      document.getElementById('mytbodyCopy').style.height=document.getElementsByClassName('my-tbody')[0].scrollHeight+'px'

      this.mainInitHeight=document.getElementsByClassName('main')[0].clientHeight;
      //获取td的实际宽高
      this.cellWidth = parseFloat(window.getComputedStyle(document.getElementsByClassName('mytd')[0]).width);
      this.cellHeight = parseFloat(window.getComputedStyle(document.getElementsByClassName('mytd')[0]).height);
      this.appNew.width=this.cellWidth;
      //获取当前时分,设置红线的位置
      let time = new Date();
      let hour=time.getHours();
      let minute=time.getMinutes();
      let time1=this.workTime1.start;//开始时间
      let startHour=time1.split(':')[0]
      let startMinute=time1.split(':')[1]
      if((hour*60+minute)<(Number(startHour)*60+Number(startMinute))){
        this.timeLineTop=0;
      }else
      if(Number(hour)>Number(this.workTime2.end.split(':')[0])||(Number(hour)==Number(this.workTime2.end.split(':')[0])&&Number(minute)>=Number(this.workTime2.end.split(':')[1]))){
        this.timeLineTop=document.getElementsByClassName('my-tbody')[0].scrollHeight;
      }else{
        let num=(Number(hour)-Number(startHour))*12+Math.ceil((Number(minute)-Number(startMinute))/5)
        this.timeLineTop=this.cellHeight*num/3;
        this.timeSetInterval=setInterval(()=>{
          this.timeLineTop+=this.cellHeight/3
        },300000)
        // 300000
      }

      this.dateNow();
    })


  },
  methods: {
    init(){
      this.appDetail.show=false;

          // this.waitList=res.data.data.determineList;
          // this.stateList=res.data.data.stateList;


      this.appChange(true)
    },
    appChange(isInit){
      if(isInit){
        this.dataListCopy=JSON.parse(JSON.stringify(this.dataList))
      }

      this.appDataCopy.appList=[];
      this.appDataCopy.appNum=[];
      this.appDataCopy.appTd=[];
      this.appDataCopy.appIndex=[];//每个td里的预约的index

      this.dataListCopy.forEach((item,index)=>{//计算每个td里有的预约数量
        //appDataCopy.appNum数组 新建的基础
        let appNumArr=[];
        for(let i=0;i<this.tdNum;i++){
          appNumArr.push(0)
        }
        this.appDataCopy.appNum.push(appNumArr)

        item.list.forEach((item2,index2)=>{
          this.appDataCopy.appNum[index].push({})
          let time1=item2.startTime;
          let time2=item2.endTime;
          let startHour=time1.split(':')[0]
          let startMinute=time1.split(':')[1]
          let endHour=time2.split(':')[0]
          let endMinute=time2.split(':')[1]
          let long=((Number(endHour)-Number(startHour))*60+(Number(endMinute)-Number(startMinute)))/15

          let top=((Number(startHour)-Number(this.workTime1.start.split(':')[0]))*60+(Number(startMinute)-Number(this.workTime1.start.split(':')[1])))/15
          for(let i=top;i<(long+top);i++){
            this.appDataCopy.appNum[index][i]+=1;
          }
        })

      })
      this.dataListCopy.forEach((item,index)=>{//处理预约td占位数组
        this.appDataCopy.appTd.push([])
        this.appDataCopy.appIndex.push([])
        let appNum=0;
        for(let i=0;i<this.tdNum;i++){
          this.appDataCopy.appTd[index].push([])
          this.appDataCopy.appIndex[index].push([])
          if(appNum<this.appDataCopy.appNum[index][i]){
            appNum=this.appDataCopy.appNum[index][i]
          }
        }
        this.liAppNum=appNum;
        if(appNum>0){
          for(let i2=0;i2<appNum;i2++){
            for(let i=0;i<this.tdNum;i++){
              this.appDataCopy.appTd[index][i].push(-1)
            }
          }
        }
      })
      this.dataListCopy.forEach((item,index)=>{//处理预约小块的宽高,上距离,左距离
        this.appDataCopy.appList.push([])
        item.list.forEach((item2,index2)=>{
          this.appDataCopy.appList[index].push({})
          let time1=item2.startTime;
          let time2=item2.endTime;
          let startHour=time1.split(':')[0]
          let startMinute=time1.split(':')[1]
          let endHour=time2.split(':')[0]
          let endMinute=time2.split(':')[1]
          let long=((Number(endHour)-Number(startHour))*60+(Number(endMinute)-Number(startMinute)))/15
          let top=((Number(startHour)-Number(this.workTime1.start.split(':')[0]))*60+(Number(startMinute)-Number(this.workTime1.start.split(':')[1])))/15


          let appNum=1;//这个范围里有几个预约
          for(let i=top;i<top+long;i++){
            if(appNum<this.appDataCopy.appNum[index][i]){
              appNum=this.appDataCopy.appNum[index][i]
            }
          }

          let leftIndex=0;//
          for(let i2=0;i2<appNum;i2++){
            let flag=false;
            for(let i=top;i<top+long;i++){
              if(this.appDataCopy.appTd[index][i][i2]!=-1){
                flag=true;
                leftIndex+=1;
                break;
              }
            }
            if(!flag){
              break;
            }
          }
          this.appDataCopy.appList[index][index2]={
            width:this.cellWidth/appNum-3,
            height: this.cellHeight*long,
            top: this.cellHeight*top,
            left: Number(this.cellWidth*index)+Number((this.cellWidth/appNum)*leftIndex),
            zindex:9,
            info:this.dataListCopy[index].list[index2]
          }
          for(let i=top;i<top+long;i++){
            this.appDataCopy.appTd[index][i][leftIndex]=1;//添加完的 把 appTd 里-1改成1
            this.appDataCopy.appIndex[index][i].push(index2);//在 appIndex 的index列 里填上 这一行td里有的预约的index
          }

        })

      })

      this.dataListCopy.forEach((item,index)=>{//再次处理预约小块的宽度
        item.list.forEach((item2,index2)=>{

          let time1=item2.startTime;
          let time2=item2.endTime;
          let startHour=time1.split(':')[0]
          let startMinute=time1.split(':')[1]
          let endHour=time2.split(':')[0]
          let endMinute=time2.split(':')[1]
          let long=((Number(endHour)-Number(startHour))*60+(Number(endMinute)-Number(startMinute)))/15
          let top=((Number(startHour)-Number(this.workTime1.start.split(':')[0]))*60+(Number(startMinute)-Number(this.workTime1.start.split(':')[1])))/15

          for(let i=top;i<top+long;i++){//
            for(let i2=0;i2<this.appDataCopy.appIndex[index][i].length;i2++){
              let index3=this.appDataCopy.appIndex[index][i][i2];
              let width=this.appDataCopy.appList[index][index2].width;
              let  left=this.appDataCopy.appList[index][index2].left;
              let left2=this.appDataCopy.appList[index][index3].left;
              if(left<left2){
                if(width+left>left2){
                  this.appDataCopy.appList[index][index2].width=left2-left-3;
                }
              }
            }
          }

        })

      })

        this.appData=JSON.parse(JSON.stringify(this.appDataCopy))


    },

    addView(row,type){
      let hour=Number(row.time.split(':')[0]);
      let minute=Number(row.time.split(':')[1]);

      hour+=parseInt(row.num/4)
      minute+=(row.num%4)*15
      if(minute>=60){
        hour+=parseInt(minute/60)
        minute=Number(minute%60)
        if(minute==0){
          minute='00'
        }
      }else
      if(minute==0){
        minute='00'
      }
      if(hour<10){
        hour='0'+hour
      }

      let endTime=hour+':'+minute;
      let data={

        startTime:row.time,
        endTime:endTime,
        patientName:'张三'
      }
      this.dataList[row.liIndex].list.push(data)
      this.appChange(true)
    },
    body_mousedown(e) {

    },
    body_mousemove(e) {
      const pgx = e.clientX;
      const pgy = e.clientY;
      const idx = this.holdIndex;
      const idx2 = this.holdIndex2;
      this.appNewShow=false;
      if (this.isHold) {
        //平移操作
        if (this.type == "title") {
          this.appDataCopy.appList[idx][idx2].zindex=19;
          //对x坐标的处理
          const cellWidth = this.cellWidth;
          let res = Math.floor((pgx - this.originX) / cellWidth) * cellWidth ;
          let widthNew=this.appData.appList[idx][idx2].left + res;
          if (widthNew >= 0 && widthNew <= cellWidth*(this.dataList.length-1)) {
            this.appDataCopy.appList[idx][idx2].left = widthNew;
            this.holdIndexNew=parseInt((this.appDataCopy.appList[idx][idx2].left+this.dataList.length)/cellWidth)//改变 列 要改变list 的index
          }
          //对x坐标处理完成

          //对y坐标的处理
          const cellHeight = this.cellHeight;
          let res2 = Math.floor((pgy - this.originY) / cellHeight) *cellHeight;
          if (this.appData.appList[idx][idx2].top + res2 >= 0 &&this.appData.appList[idx][idx2].top + res2 <= parseFloat(document.getElementsByClassName('my-tbody')[0].scrollHeight)-this.appData.appList[idx][idx2].height) {
            this.appDataCopy.appList[idx][idx2].top =this.appData.appList[idx][idx2].top + res2;
            let num=parseInt(this.appDataCopy.appList[idx][idx2].top/cellHeight);
            let numHour=parseInt(num/4)
            let numMinute=Number((num%4)*15)
            let time1=this.workTime1.start;//工作开始时间
            let startHour=time1.split(':')[0]
            let startMinute=time1.split(':')[1]
            let hour=Number(startHour)+Number(numHour);
            let minute=Number(startMinute)+Number(numMinute)
            if(minute>=60){
              hour+=parseInt(minute/60)
              minute=Number(minute%60)
              if(minute==0){
                minute='00'
              }
            }else
            if(minute==0){
              minute='00'
            }
            if(hour<10){
              hour='0'+hour
            }
            this.dataListCopy[idx].list[idx2].startTime=hour+':'+minute;
            let num2=parseInt((this.appDataCopy.appList[idx][idx2].height+1)/cellHeight);
            let numHour2=parseInt(num2/4)
            let numMinute2=Number((num2%4)*15)
            let hour2=Number(hour)+Number(numHour2);
            let minute2=Number(minute)+Number(numMinute2)
            if(minute2>=60){
              hour2+=parseInt(minute2/60)
              minute2=minute2%60
              if(minute2==0){
                minute2='00'
              }
            }else
            if(minute2==0){
              minute2='00'
            }
            if(hour2<10){
              hour2='0'+hour2
            }

            this.dataListCopy[idx].list[idx2].endTime=hour2+':'+minute2;

          }
        }
        //拉伸操作
        if (this.type == "footline") {
          this.appDataCopy.appList[idx][idx2].zindex=19;
          let height=e.clientY - this.originY + this.appData.appList[idx][idx2].height;
          if(height < parseFloat(document.getElementsByClassName('my-tbody')[0].scrollHeight)-this.appDataCopy.appList[idx][idx2].top){
            this.appDataCopy.appList[idx][idx2].height = height

          }

        }
        //新建预约
        if (this.type == "add") {

          if(Math.abs(e.clientY - this.originY)>10){
            const cellWidth = this.cellWidth;
            this.appNewIndex=Math.floor((this.offsetX) / cellWidth);
            if(this.appNewIndex>this.dataList.length-1){//点击到了空白的列

            }else{
              this.appNewShow=true;
              //对x坐标处理

              this.appNew.left=Math.floor((this.offsetX) / cellWidth) *cellWidth;

              //对y坐标的处理
              const cellHeight = this.cellHeight;

              this.appNew.top=Math.floor((this.offsetY) / cellHeight) *cellHeight;

              if(Math.ceil((pgy - this.originY) / cellHeight)<=1){
                this.appNew.height=cellHeight;
                this.appNewHeightNum=1;
                this.appNew.timeBottom=-20;
              }else{
                this.appNew.height=Math.ceil((pgy - this.originY) / cellHeight) *cellHeight;
                this.appNewHeightNum=Math.ceil((pgy - this.originY) / cellHeight) ;
                this.appNew.timeBottom=0;
              }

              let num=parseInt(this.appNew.top/cellHeight);
              let numHour=parseInt(num/4)
              let numMinute=Number((num%4)*15)
              let time1=this.workTime1.start;//工作开始时间
              let startHour=time1.split(':')[0]
              let startMinute=time1.split(':')[1]
              let hour=Number(startHour)+Number(numHour);
              let minute=Number(startMinute)+Number(numMinute)
              if(minute>=60){
                hour+=parseInt(minute/60)
                minute=Number(minute%60)
                if(minute==0){
                  minute='00'
                }
              }else
              if(minute==0){
                minute='00'
              }
              if(hour<10){
                hour='0'+hour
              }
              this.appNew.startTime=hour+':'+minute;
              let num2=this.appNewHeightNum;
              let numHour2=parseInt(num2/4)
              let numMinute2=Number((num2%4)*15)
              let hour2=Number(hour)+Number(numHour2);
              let minute2=Number(minute)+Number(numMinute2)
              if(minute2>=60){
                hour2+=parseInt(minute2/60)
                minute2=minute2%60
                if(minute2==0){
                  minute2='00'
                }
              }else
              if(minute2==0){
                minute2='00'
              }
              if(hour2<10){
                hour2='0'+hour2
              }
              this.appNew.endTime=hour2+':'+minute2;
            }

          }



        }

      }
    },
    body_mouseup(e) {
      const idx = this.holdIndex;
      const idx2 = this.holdIndex2;
      this.isHold = false;
      let isChange=false;
      if(this.type == "title"||this.type == "footline"){

        this.appDataCopy.appList[idx][idx2].zindex=9;
        if(this.appDataCopy.appList[idx][idx2].height!=this.appData.appList[idx][idx2].height||this.appDataCopy.appList[idx][idx2].top!=this.appData.appList[idx][idx2].top||this.appDataCopy.appList[idx][idx2].left!=this.appData.appList[idx][idx2].left){
          this.confirm=true;
          isChange=true;

        }
      }
      if (this.type == "footline") {
        const n = this.appDataCopy.appList[idx][idx2].height;
        if(Math.ceil(n / this.cellHeight) * this.cellHeight <= parseFloat(document.getElementsByClassName('my-tbody')[0].scrollHeight)-this.appDataCopy.appList[idx][idx2].top){
          this.appDataCopy.appList[idx][idx2].height = Math.ceil(n / this.cellHeight) * this.cellHeight;
        }else{
          this.appDataCopy.appList[idx][idx2].height = (Math.ceil(n / this.cellHeight)-1) * this.cellHeight;
        }
        let num=parseInt(this.appDataCopy.appList[idx][idx2].height/this.cellHeight);
        let numHour=parseInt(num/4)
        let numMinute=(num%4)*15
        let time1=this.dataListCopy[idx].list[idx2].startTime;//本次开始时间
        let startHour=time1.split(':')[0]
        let startMinute=time1.split(':')[1]
        let hour=Number(startHour)+numHour;
        let minute=Number(startMinute)+numMinute
        if(minute>=60){
          hour+=parseInt(minute/60)
          minute=Number(minute%60)
          if(minute==0){
            minute='00'
          }
        }else
        if(minute==0){
          minute='00'
        }
        if(hour<10){
          hour='0'+hour
        }
        this.dataListCopy[idx].list[idx2].endTime=hour+':'+minute;
        this.holdInfo=JSON.parse(JSON.stringify(this.dataListCopy[idx].list[idx2]))
      }

      if (this.type == "title") {
        this.holdInfo=JSON.parse(JSON.stringify(this.dataListCopy[idx].list[idx2]))
        if(this.holdIndexNew!=idx){
          //改变了列 要改变list 的index

          let newAppDataLi=this.appDataCopy.appList[idx][idx2]
          let newDataLi=this.dataListCopy[idx].list[idx2]
          this.appDataCopy.appList[idx].splice(idx2,1);
          this.dataListCopy[idx].list.splice(idx2,1);

          if(this.dataListCopy[this.holdIndexNew].list.length==0){
            this.appDataCopy.appList[this.holdIndexNew].push(newAppDataLi);
            this.dataListCopy[this.holdIndexNew].list.push(newDataLi);
          }else{
            let start=newDataLi.startTime;
            let hour=Number(start.split(':')[0])
            let minute=Number(start.split(':')[1])
            let startNum=hour*60+minute
            //高度有重叠 时间比较先后 环处理 start 小的在前,并且宽度变化
            for(let i=0;i<this.dataListCopy[this.holdIndexNew].list.length;i++){
              let start2=this.dataListCopy[this.holdIndexNew].list[i].startTime;
              let hour2=Number(start2.split(':')[0])
              let minute2=Number(start2.split(':')[1])
              let startNum2=hour2*60+minute2

              if(startNum<startNum2){
                this.appDataCopy.appList[this.holdIndexNew].splice(i,0,newAppDataLi);
                this.dataListCopy[this.holdIndexNew].list.splice(i,0,newDataLi);
                break;
              }
              if(i==this.dataListCopy[this.holdIndexNew].list.length-1){
                this.appDataCopy.appList[this.holdIndexNew].push(newAppDataLi);
                this.dataListCopy[this.holdIndexNew].list.push(newDataLi);
                break;
              }
            }
          }
        }
      }
      if (this.type == "add"){
        if(this.appNewShow){
          let row={
            id:'',
            date:this.mydate,
            time:this.appNew.startTime,
            num:this.appNewHeightNum,
            liIndex:this.appNewIndex,



          }
          // this.$refs.viewAdd.addShow(row,'add')
          this.addView(row,'add')
        }
      }
      if(isChange){

        this.appChange();
        this.appDetail.show=false;
      }
      this.type = "";
      window.event? window.event.cancelBubble = true : e.stopPropagation();
    },
    add_mousedown(e){
      this.isHold = true;
      this.type = "add";
      this.offsetX=e.offsetX;
      this.offsetY=e.offsetY;
      this.originX = e.clientX;
      this.originY = e.clientY;
      this.appDetail.show=false;
    },

    box_mousedown(e){
      this.isChange=false;
      window.event? window.event.cancelBubble = true : e.stopPropagation();
    },
    detail_mousedown(e){
      this.appDetail.show=true;
      this.appDetail.tab=1;

      window.event? window.event.cancelBubble = true : e.stopPropagation();
    },
    box_mouseup(index,index2,e){
      this.holdIndex = index;
      this.holdIndex2 = index2;
      this.appDetail.show=true;
      this.appDetail.tab=1;
      if(index>3){
        this.appDetail.left=this.appData.appList[index][index2].left-265;
      }else{
        this.appDetail.left=this.appData.appList[index][index2].left+this.cellWidth+80;
      }


      if(e.clientY>350){
        this.appDetail.top=e.clientY-350;
      }else{
        this.appDetail.top=0;
      }

      this.appDetail.info=this.dataList[index].list[index2];

    },
    title_mousedown(index,index2, e) {
      this.appDetail.show=false;
      this.isHold = true;
      this.appDataCopy=JSON.parse(JSON.stringify(this.appData))
      this.dataListCopy=JSON.parse(JSON.stringify(this.dataList))
      this.type = "title";
      this.holdIndex = index;
      this.holdIndex2 = index2;
      this.holdIndexNew=index;
      this.originX = e.clientX;
      this.originY = e.clientY;
    },
    footline_mousedown(index,index2, e) {
      this.appDetail.show=false;
      this.isHold = true;
      this.appDataCopy=JSON.parse(JSON.stringify(this.appData))
      this.dataListCopy=JSON.parse(JSON.stringify(this.dataList))
      this.type = "footline";
      this.holdIndex = index;
      this.holdIndex2 = index2;
      this.originY = e.clientY;
    },
    cancel_confirm(){
      this.appDataCopy=JSON.parse(JSON.stringify(this.appData))
      this.dataListCopy=JSON.parse(JSON.stringify(this.dataList))
      this.confirm=false;
      this.appChange();
    },
    save_confirm(){
      this.appData=JSON.parse(JSON.stringify(this.appDataCopy))
      this.dataList=JSON.parse(JSON.stringify(this.dataListCopy))
      this.confirm=false;
      this.appChange(true)
    },



    dateNow(){
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      if (month < 10) {
        month = "0" + month;
      }
      if (day < 10) {
        day = "0" + day;
      }
      this.mydate = year + "-" + month + "-" + day;

      this.nowDate=new Date(this.mydate).getTime()
      this.init();
    },
    waitDetail(info,e){

      this.appDetail.show=true;
      this.appDetail.tab=1;


      this.appDetail.left=document.getElementsByClassName('my-table')[0].clientWidth-325;

      this.appDetail.top=e.clientY-200;


      if(this.appDetail.top<0){
        this.appDetail.top=0;
      }

      this.appDetail.info=info;
      window.event? window.event.cancelBubble = true : e.stopPropagation();
    },

    closeAppdetail(){
      if(this.appDetail.show){
        this.appDetail.show=false;
      }
    },

    detail_edit(){
      let row={
        id:this.appDetail.info.id,
        date:this.mydate,
        time:this.appNew.startTime,
        num:this.appNewHeightNum
      }
      this.$refs.viewAdd.addShow(row,'edit')
    },


    myCellBoxScroll(){
      this.appDetail.show=false;
    },


  },
  beforeDestroy() {
    clearInterval(this.timeSetInterval);
    this.timeSetInterval = null;
  }
}
</script>
<style lang="less" scoped>
  *{
    box-sizing: border-box;
  }
  ul,li,p{
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .main{
    height:100%;
    padding: 20px;
    position: relative;
  }
  .body {
    position: relative;
    height:calc(~"100% - 50px");
    .my-table{
      width:calc(~"100% - 170px");
      float:left;
      height:100%;
      -moz-user-select: none;
      -webkit-user-select: none;
      user-select: none;
      .my-thead{
        background: #E4E4E4;
        display: flex;
        .my-thead-left{
          line-height:45px;
          float:left;
          text-align:center;
          width:60px;
        }
        ul{
          flex: 1;
          li{
            cursor: pointer;
            width:14.28%;
            line-height:45px;
            float:left;
            text-align:center;
            font-family: PingFangBold, sans-serif;
          }
        }

      }
      .my-tbody{
        position: relative;
        height:calc(~"100% - 45px");
        overflow-y: scroll;

        .my-tr{
          display: flex;
          .my-hour{
            width:30px;
            float:left;
            height:120px;
            line-height:120px;
            background: #6D74B7;
            color:#fff;
            text-align:center;
          }
          .my-minute-box{
            width:30px;
            float:left;
            height:120px;
            .my-minute{
              height:30px;
              line-height:29px;
              text-align:center;
              font-size:12px;
              color:#6D74B7;
              border-right:1px solid #BBC1C9;
              border-bottom:1px solid #EDEDED;
            }
            .my-minute:last-child{
              border-bottom:1px solid #BBC1C9;
            }
          }
          .my-cell-box{
            flex:1;
            .my-cell-lie{
              width:14.28%;
              float:left;

              ul{
                .bg-gary{
                  background: #A9A9A9;
                  border-bottom:1px solid #A9A9A9;
                }
                li{
                  height:30px;
                  text-align:center;
                  border-right:1px solid #BBC1C9;
                  border-bottom:1px solid #EDEDED;
                }
                li:last-child{
                  border-bottom:1px solid #BBC1C9;
                }
              }
            }
          }

        }
        .my-tbody-copy{
          position: absolute;
          width: calc(~"100% - 60px");
          height: 100%;
          top:0;
          left:60px;
          .app-new{
            position: absolute;
            display: flex;
            flex-direction: column;
            border-radius: 15px;
            border-bottom-left-radius: 0;
            overflow: hidden;
            transform: rotate(0deg);
            left:0;
            z-index: 19;
            background: #fff;
            width:100%;
            border:1px solid #1796b0;
            color:#000;
            cursor: s-resize;
            height:20px;
            p{
              font-size:16px;
              font-weight: bold;
              padding-left:5px;
            }
            .app-new-time{
              position: absolute;
              right:10px;
              bottom:0;
              font-size:10px;
              text-align: right;
            }
          }
          .my-time-line{
            width:100%;
            height:1px;
            background: #FF3E89;
            position: absolute;
            left:0;
            top:0;
            z-index: 8;
          }
          .my-time-line:after{
            content: '';
            display: block;
            width:7px;
            height:7px;
            position: absolute;
            left:-3px;
            top:-3px;
            background: #FF3E89;
            border-radius:50%;
          }
        }
      }

    }


    .chooser {
      position: absolute;
      display: flex;
      flex-direction: column;
      border-radius: 15px;
      border-bottom-left-radius: 0;
      overflow: hidden;
      transform: rotate(0deg);
      .title {
        background: #ccc;
        height: 30px;
        cursor: move;
        width:100%;
        color:#fff;
        padding:0 5px;
        padding-top:5px;
        line-height:22px;
        font-weight: bold;
      }
      .content {
        background: #8FC4FE;
        width:100%;
        flex: 1;
        padding:0 5px;
        color:#000;
        position: relative;
        .content-time{
          position: absolute;
          right:10px;
          bottom:2px;
        }
      }
      .footline {
        position: absolute;
        bottom:0;
        left:0;
        height: 2px;
        width:100%;
        cursor: s-resize;
      }
    }

  }

  .tip-box{
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.3);
    left:0;
    top:0;
    z-index: 99;
    .tip{
      width: 400px;
      height: 200px;
      background: #fff;
      position: absolute;
      left:0;right:0;
      margin:0 auto;
      top:300px;
      border-radius: 6px;
      text-align: center;
      p{
        font-size: 24px;

        line-height: 130px;
      }
      .tip-btn{
        display: inline-block;
        width: 80px;
        line-height: 30px;
        font-size: 16px;
        border-radius: 4px;
        border: 1px solid #ccc;
        cursor: pointer;
      }
    }
  }
</style>



